$header-color: $color-1--3;

.header {
    display: block;
    position: relative;
    width: 100%;

    @include breakpoint(medium down) {
        padding: 0;
    }

    &.-has-image {
        background: none;
        padding: 0;
    }

    &.-map {
        padding: 0;
    }
}
