/*
    Hide the datepickers associated input by using a className
*/
.fd-hidden-input {
    display: none;
}

/*
    Screen reader class - used to hide spans inside each cell from the visual
    display. See: http://webaim.org/techniques/css/invisiblecontent/
*/
.fd-screen-reader {
    position: absolute;
    left: -999em;
    top: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
    -moz-outline: 0 none;
    outline: 0 none;
}

/*
    The wrapper div
*/
.date-picker {
    position: absolute;
    z-index: 9999;
    text-align: center;
    /*
    Change the font-size to suit your needs. The following line is for the demo
    that has a 14px font-size defined on the body tag.

    All other rules are "em" based and inherit from the font-size set here.
    */
    font: 900 0.8em/1em "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Verdana, Arial, "Lucida Grande", sans-serif;
    background: transparent;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
    /*
    Set the fade in/out animation time here. This is used for browsers that
    support CSS transitions. Non-supporting browsers receive the fade effect
    using a Javascript fallback.
    */
    opacity: 1;
    -webkit-transition: opacity .75s ease-in-out;
    -moz-transition: opacity .75s ease-in-out;
    -ms-transition: opacity .75s ease-in-out;
    -o-transition: opacity .75s ease-in-out;
    transition: opacity .75s ease-in-out;
}

.date-picker div {
    border: 1px solid rgba(255, 255, 255, 0.8);
    border-radius: 0.8em;
}

/*
    The following rule is used whenever CSS Animations are supported by the
    browser
*/
.date-picker.fd-dp-fade {
    opacity: 0;
}

/*
    Styles for the static datePickers
*/
.static-datepicker {
    position: relative;
    top: 5px;
    left: 0;
    margin-bottom: 1.5em;
}

/*
    The iframe hack to cover selectlists in IE6
*/
.iehack {
    position: absolute;
    background: #fff;
    z-index: 9998;
    padding: 0;
    margin: 0;
    border: 0;
    display: none;
}

/*
    The activation "button" created beside each input for popup datePickers
*/
.date-picker-control span {
    display: block;
    width: 18px;
    height: 18px;
    overflow: hidden;
    margin: auto 0 0.3rem;
    resize: none;
    outline: none;
}

/*
    The hover effect on the activation button
*/
.date-picker-button-active span,
.date-picker-control:focus span,
.date-picker-control:hover span,
.date-picker-control:active span {
    outline: none;
    box-shadow: 0 0 5px rgba(40, 130, 170, 0.7);
    border-radius: 2px;
}

/*
    A base64 encoded image-sprite is used for the activation button. IE6 and 7
    get fed "normal" png images within the rule that follows.
*/
.date-picker-control:link,
.date-picker-control:visited {
    position: absolute;
    display: -moz-inline-box;
    border: 0 none;
    padding: 0;
    margin: 0 4px;
    background-color: transparent;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: 0 -72px;
    min-width: 18px;
    line-height: 1;
    cursor: pointer;
    visibility: visible;
    text-decoration: none;
    vertical-align: text-bottom;
    resize: none;
    bottom: 12px;
    right: 6px;
}

