    Common TD & TH styling
*/
.date-picker td,
.date-picker tbody th {
    padding: 0;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    background-position: 0 -35px;
    width: 3em;
    height: 3em;
    overflow: hidden;
    outline: transparent none 0px;
    border: 1px solid #cfcfcf;
    text-transform: none;
    font-weight: normal;
    text-shadow: 0 1px #fff;
    border-radius: 3px;
}

