#tx-solr-faceting,
#tx-solr-sorting {
    margin-bottom: 6rem;

    h2 {
        color: lighten($color-black, 28.5); // #494949
        font-size: 2rem;
        margin-top: 0;
        text-transform: uppercase;
    }
}

#tx-solr-faceting {

    .facets {
        padding: 0;
        margin: 0;

        .csc-header {
            //padding: 3px 0 3px 10px;
        }

        .facet-option-list {
            padding: 1rem 2rem;
            margin-bottom: 2rem;
            @include clear;
        }

        .facet-type-numericRange .facet-option-list {
            width: 85%;
        }

        .facet-label {
            border-bottom: 4px solid $color-1--2;
            color: lighten($color-black, 28.5); // #494949
            font-size: 1.8rem;
            font-weight: bold;
            margin: 0.5em 0;
            padding: 0.25em 0.5em;
            position: relative;

            &::after {
                @include absolute(100%, null, null, 1rem);
                border-color: $color-1--2 transparent transparent transparent;
                border-style: solid;
                border-width: 10px 10px 0;
                content: '';
                display: block;
                height: 0;
                width: 0;
            }
        }

        .facet-result-count {
            color: $color-2--3;
        }

        .facet-type-dateRange {
            fieldset {
                margin: 0;
                padding: 0;
            }
        }

        .dateselector-label {
            display: block;
            font-weight: $normal;
            position: relative;
            text-transform: none;

            span {
                color: $color-3--3;
                display: block;
                font-style: italic;
            }
        }
        .dateselector {
            display: block;
            width: 100%;
        }

        li {
            list-style-type: none;
        }
    }
}



#tx-solr-facets-in-use {
    margin-bottom: 6rem;
    li {
        list-style-type: none;
        margin-left: 10px;

        &:not(.facets-remove-all) a {
            text-decoration: none;
            //@include icon-after($fa-var-times);

            &::after {
                content: '';
                font-size: em(1.3, 1.7);
                line-height: em(1.7, 1.3);
                margin-left: 0.5rem;
            }

            @include on-event() {
                text-decoration: underline;
            }
        }
    }
}

#tx-solr-facets-in-use .facets-remove-all {
    margin: 2rem 0 0;
    padding: 0;

    &::before {
        content: none;
    }

    a {
        background-color: transparent;
        border: solid 1px rgba($color-3--2, 0.5);
        color: $color-black;
        display: block;
        font-family: $typo-2;
        line-height: (16/14);
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        width: 100%;
        @include transition($duration: 100ms);

        @include btn-size();
        //@include btn-icon('before', $fa-var-times-circle);
        &::before {
            content: '';
            color: $color-2--2;
            margin-right: 0.5rem;
        }

        @include on-event() {
            background-color: $color-1--2;
            border-color: transparent;
            color: $color-white;
            cursor: pointer;
            text-decoration: none;

            &::before,
            &::after {
                color: inherit !important;
            }
        }
    }
}

.facet-type-options,
#tx-solr-facets-in-use,
#tx-solr-sorting {
    $content-list-fz: 1.7;

    ul {
        color: $color-black;
        font-size: #{$content-list-fz}rem;
        font-weight: $light;
        list-style: none;
        margin: 2rem 0 1.5rem 0;
        padding: 0;
    }

    li {
        line-height: 1.4;
        margin-left: em(1.5, $content-list-fz);
        padding: 0 0 em(0.5, $content-list-fz) em(1.25, $content-list-fz);
        //@include icon-before($fa-var-angle-right);
        position: relative;

        &::before {
            content: '';
            color: $color-1--2;
            @include absolute($left: 0);
        }
    }

    a {
        color: lighten($color-black, 28.5); // #494949
        font-family: $typo-1;
        text-decoration: underline;

        @include on-event() {
            text-decoration: none;
        }
    }
}

#tx-solr-search .tx-solr-search-word {
    font-weight: bold;
}

#tx-solr-search .results-list {
    margin-top: 10px;
}

#tx-solr-search .results-list li {
    list-style: none inside;
    margin-left: 0;
}

.results-entry {
    //border: 1px solid $color-1--3;
    margin: 0 0 2rem;
    padding: 1rem;
    background-color: $color-2--4;
}

.results-elevated {
    background: #ffffef;
    border: 1px solid #cfcecf;
    padding: 5px;
}

.results-elevated-label {
    color: #cfcecf;
    font-size: 50%;
    padding-bottom: 5px;
}

.results-topic {
    font-weight: bold;
    margin-bottom: 5px;
    font-size: 2.6rem;
    margin-top: 0.25em;

    a {
        color: $color-1--3;

        &:hover,
        &:active {
            text-decoration: underline;
        }
    }
}

.result-content {
    clear: both;
    font-size: 1.7rem;

    a {
        color: $color-black;
        font-weight: $bold;

        @include on-event() {
            text-decoration: underline;
        }
    }
}

.result-date {
    color: $color-3--3;
    font-family: $typo-2;
    font-size: 1.4rem;
    font-weight: bold;
    margin-bottom: 0;
}

.results-highlight {
    background: #fdee53;
}

#tx-solr-search ul.references li {
    list-style-type: disc;
}

.relevance {
    font-family: $typo-2;
    height: 20px;

    &-label,
    &-bar,
    &-percent {
        float: left;
        margin-right: 5px;
    }

    &-label,
    &-percent {
        font-size: 1.4rem;
    }

    &-bar {
        padding-top: 4px;
    }
}


.tx-solr-relevance-bar {
    background-color: $color-3--4;
    font-size: 1px;
    height: 8px;
    width: 65px;
}

.tx-solr-relevance,
.tx-solr-relevance-fill {
    float: left;
    height: 8px;
    margin-right: 0 !important;
    padding: 0;
}

.tx-solr-relevance {
    background-color: $color-1--2;
}

.tx-solr-relevance-fill {
    background-color: $color-3--4;
}

#tx-solr-frequent-searches ul {
    padding-right: 10px;
}

#tx-solr-frequent-searches li {
    list-style-type: none;
    margin-left: 10px;
    display: inline;
}

@for $i from 14 through 31 {
    .tx-solr-frequent-term-#{$i} {
        font-size: #{$i}px;
    }
}

.tx-solr-loader {
    height: 100px;
    left: 50%;
    position: fixed;
    top: 35%;
    width: 100px;
    z-index: 10000;
    background: url('data:image/gif;base64,');
}

.search-no-results {
    &__content {
        //@include icon-before($fa-var-info-circle);
        align-items: center;
        display: flex;
        flex-direction: column;
        position: relative;

        @include breakpoint(small down) {
            margin-bottom: 5rem;
        }

        &::before {
            content: '';
            color: $color-2--3;
            font-size: 5rem;
            font-weight: $bold;
        }
    }

    &__text {
        font-size: 1.6rem;
        font-style: italic;
        margin: 0.8rem 0;
        text-align: center;

        a {
            color: $color-3--3;
            text-decoration: underline;

            @include on-event() {
                text-decoration: none;
            }
        }
    }
}
/* stylelint-enable */
